@font-face {
	font-family: Raleway-Medium;
	src: local("Raleway-Medium"), url(./Raleway-Medium.ttf);
}

section {
	margin: 1rem;
	padding: 2rem;
	text-align: center;
	background-color: #333;
	border-radius: 0.5rem;
	color: #ccc;
	font-weight: bold;
	box-shadow: 0 0 0.5rem whitesmoke;
}

section h2 {
	font-weight: 900;
	margin-top: 0;
	margin-bottom: 1rem;
	border-bottom-style: solid;
	border-bottom-width: 1px;
}
section:nth-child(1) h2 {
	color: lightcoral;
}
section:nth-child(2) h2 {
	color: rgb(221, 180, 77);
}
section:nth-child(3) h2 {
	color: lightgreen;
}
section:nth-child(4) h2 {
	color: lightskyblue;
}
section:nth-child(5) h2 {
	color: rgb(130, 255, 238);
}
section:nth-child(6) h2 {
	color: rgb(130, 174, 255);
}
section:nth-child(7) h2 {
	color: rgb(188, 130, 255);
}
section:nth-child(8) h2 {
	color: rgb(255, 130, 238);
}

input {
	margin: 0.5rem;
	padding: 0.6rem;
	border-radius: 2rem;
	border-style: solid;
	border-width: 0.15rem;
	border-color: #555;
	background-color: #999;
	color: #333;
	font-family: Raleway-Medium;
	font-size: 1.2rem;
}

input:focus {
	outline: 0;
}

input:hover {
	border: 0.15rem solid rgb(192, 128, 255);
}

textarea {
	margin: 0.5rem;
	padding: 0.8rem;
	border-radius: 2rem;
	border-style: solid;
	border-width: 0.15rem;
	border-color: #555;
	background-color: #999;
	color: #333;
	font-family: Raleway-Medium;
	font-size: 1.2rem;
}

textarea:focus {
	outline: 0;
}

textarea:hover {
	border: 0.15rem solid rgb(192, 128, 255);
}

textarea::-webkit-scrollbar {
	width: 12px; /* width of the entire scrollbar */
}

textarea::-webkit-scrollbar-track {
	background: #555; /* color of the tracking area */
}

textarea::-webkit-scrollbar-thumb {
	background-color: #999;
	border-radius: 20px;
	border: 3px solid #555;
}

img {
	width: 10rem;
	border-radius: 0.5rem;
	background-color: rgb(47, 47, 47);
	box-shadow: 0 0 0.3rem whitesmoke;
}

img:hover {
	opacity: 0.7;
}

button {
	align-self: flex-end;
	gap: 1rem;
	display: flex;
	align-items: center;
	border-style: none;
	margin: 0.5rem;
	margin-top: 1.5rem;
	padding: 1rem;
	padding-right: 1.5rem;
	background-image: linear-gradient(45deg, #654ea3, #eb5895);
	color: rgb(255, 217, 184);
	font-size: 1rem;
	font-weight: 900;
	border-radius: 2rem;
}

button:hover {
	transform: scale(1.05);
}

button .icon {
	font-size: 2rem;
}

button .pipe {
	font-size: 1.5rem;
	border-left: 0.1rem solid rgb(255, 217, 184);
}

button[type="button"] {
	align-self: flex-start;
	padding: 0.7rem 2rem 0.7rem 2rem;
}

/* {section.Save} */
/* Personal Information {.Save form.editableFields-false} */

section.Save form.editableFields-false {
	display: grid;
	grid-template-columns: max-content max-content;
	justify-items: center;
	column-gap: 1rem;
}

section.Save form.editableFields-false > div {
	display: grid;
}

section.Save form.editableFields-false > div > div {
	justify-self: left;
	margin-top: 1rem;
}

.notEditable0 {
	grid-row: 1 / 2;
	grid-column: 1 / 2;
}

.notEditable1 {
	grid-row: 2 / 3;
	grid-column: 1 / 2;
}

.notEditable2 {
	grid-row: 1 / 3;
	grid-column: 2 / 3;
	align-self: center;
	width: auto;
}

section form.editableFields-false > button[type="submit"] {
	grid-column: 1 / 3;
	justify-self: flex-end;
}

/* Other Sections {.Save form}*/

section .singInfoInter {
	margin-top: 2rem;
	margin-bottom: 1rem;
	display: grid;
	grid-template-columns: 1fr 2fr;
	font-size: 1.2rem;
}

section.Save form.editableFields-true {
	display: flex;
	flex-direction: column;
}

section.Save form.editableFields-true > div {
	display: grid;
	grid-template-columns: 1fr 2fr;
	align-items: center;
	border-bottom-style: solid;
	border-bottom-width: 1px;
	border-bottom-color: #999;
}

section.Save form.editableFields-true > button[type="submit"] {
	grid-row: 4 / 5;
	grid-column: 2 / 3;
}

/* Experience {.Save form} */
section.Save.subInfo-true.fromTos-true .singInfoInter {
	margin-top: 2rem;
	margin-bottom: 2rem;
	display: grid;
	grid-template-columns: 0.5fr 0.5fr 3fr 1fr;
	font-size: 1.2rem;
}

section.Save.subInfo-true.fromTos-true form.editableFields-true > div {
	margin-top: 1rem;
	margin-bottom: 1rem;
	display: grid;
	grid-template-columns: 1fr 3fr min-content;
	grid-template-rows: max-content max-content;
	align-items: center;
	border-top-style: solid;
	border-top-width: 1px;
	border-top-color: #999;
	border-bottom-style: solid;
	border-bottom-width: 1px;
	border-bottom-color: #999;
}

section.Save.subInfo-true.fromTos-true
	form.editableFields-true
	> div
	> :nth-child(1) {
	grid-row: 1 / 2;
	grid-column: 1 / 2;
}

section.Save.subInfo-true.fromTos-true
	form.editableFields-true
	> div
	> :nth-child(2) {
	grid-row: 2 / 3;
	grid-column: 1 / 2;
}

section.Save.subInfo-true.fromTos-true
	form.editableFields-true
	> div
	> :nth-child(3) {
	grid-row: 1 / 3;
	grid-column: 2 / 3;
}

section.Save.subInfo-true.fromTos-true
	form.editableFields-true
	> div
	> :nth-child(4) {
	grid-row: 1 / 3;
	grid-column: 3 / 4;
	display: grid;
	grid-template-rows: min-content min-content;
	grid-template-columns: 6rem 6rem;
	text-align: left;
}

section.Save.fromTos-true form.editableFields-true .fromTo > label {
	grid-row: 1 / 2;
}

section.Save.fromTos-true form.editableFields-true .fromTo input {
	text-align: center;
}

/* {section.Edit} */
/* {.personal.Edit} */

section.personal.Edit > div {
	display: grid;
	grid-template-columns: max-content max-content;
	align-items: center;
}

section.personal.Edit > div > div:nth-child(1) {
	grid-column: 1 / 2;
	grid-row: 1 / 2;
	align-self: flex-end;
	justify-self: center;
}
section.personal.Edit > div > div:nth-child(1) div .nameOrTitle {
	font-size: 1.5rem;
	color: rgb(188, 130, 255);
}

section.personal.Edit > div > div:nth-child(2) div .nameOrTitle {
	font-size: 1.3rem;
}

section.personal.Edit > div > div:nth-child(2) {
	grid-column: 1 / 2;
	grid-row: 2 / 3;
	align-self: flex-start;
	justify-self: center;
}

section.personal.Edit > div > div:nth-child(3) {
	grid-column: 2 / 3;
	grid-row: 1 / 3;
	margin-top: 2rem;
	margin-bottom: 2rem;
	margin-left: 1rem;
}

section.personal.Edit .item {
	grid-column: 1 / 3;
}

section.personal.Edit .field {
	display: flex;
	gap: 1rem;
	margin-left: 4rem;
}

section.personal.Edit > div button {
	grid-column: 1 / 3;
	width: max-content;
	justify-self: center;
}

/* {.Edit .item} (That is for both {.subInfo-false.fromTos-false} and {.subInfo-true.fromTos-true} */

section.Edit .item > div {
	text-align: start;
}

section.Edit .item > div:nth-child(1) > div:nth-child(1) {
	color: rgb(162, 162, 255);
	font-size: 1.3rem;
}

section.Edit .item > div > div:nth-child(1) svg {
	position: relative;
	top: -0.2rem;
	font-size: 0.6rem;
}

section.Edit .item > div .info {
	margin-bottom: 1rem;
	font-size: 1.1rem;
	white-space: pre;
	color: #ccc;
}

/* {.Edit.subInfo-true.fromTos-true} */
section.Edit.subInfo-true.fromTos-true .item {
	display: grid;
	grid-template-columns: 1fr max-content;
	gap: 1rem;
	margin-bottom: 1.5rem;
}

section.Edit.subInfo-true.fromTos-true .item > div:nth-child(1) {
	padding-right: 1rem;
	border-right: 1px solid #999;
	border-radius: 0.5rem;
}

section.Edit.subInfo-true.fromTos-true .item > div .subInfo {
	white-space: pre;
	color: #aaa;
}

section.Edit.subInfo-true.fromTos-true .item .fromTo {
	align-self: center;
	display: flex;
}

section.Edit.subInfo-true.fromTos-true .item .fromTo div {
	display: flex;
	align-items: center;
}

section.Edit.subInfo-true.fromTos-true .item .fromTo :nth-child(1)::after {
	content: "~";
}
